@import url("https://fonts.cdnfonts.com/css/pokemon-hollow");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  background: black;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
}

h2,
h1,
p {
  font-weight: 200;
}
.header h1 {
  font-family: "Pokemon Solid", sans-serif;
  font-size: 5rem;
  color: #ffffff;
  text-align: center;
  padding: 1rem;
  margin: 1rem;
}

.pokedex,
.pokemon {
  max-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  margin: 1rem;
  background-color: black;
  /* box-shadow: 0 8px 32px 0 rgba(6, 8, 28, 0.37); */
  /* border: 1px solid white; */
  border-radius: 2rem;
}

.searchbar {
  display: flex;
  margin: 0.5rem;
  background-color: rgb(0, 0, 0);
  border: 1px solid white;
  border-radius: 2rem;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.619);
}

input {
  background-color: rgba(196, 196, 196, 0.648);
  border: none;
  font-size: 1rem;
  border-radius: 1rem;
  padding: 0.5rem;
}

button {
  background-color: rgb(69, 69, 69);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 1rem;
  padding: 0.5rem;
  margin: 0.5rem;
}

.pokemonlist-container {
  justify-content: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  overflow-y: scroll;
  scrollbar-width: none;
}

.pokemonlist-card {
  width: 150px;
  height: 150px;
  /* background-color: #d6848a54; */
  border: 1px solid white;
  border-radius: 1rem;
  flex-direction: column;
  border-radius: 1rem;
  align-items: center;
  text-align: center;
  display: flex;
  padding: 0.5rem;
  /* border: 1px solid #642c34; */
}

.pokemonlist-card p {
  padding: 0.5rem 0.5rem;
  color: white;
}

.pokemonlist-card img {
  border-radius: 1rem;
}

.pokemonlist-card:hover {
  background-color: #54545496;
}

.pokemoncard-container {
  align-items: center;
  flex-direction: column;
  color: white;
  border: 1px solid white;
  border-radius: 15px;
  padding: 2rem;
}

.pokemoncard-container img {
  width: 150px;
  object-fit: cover;
  /* background-color: #d6848a54; */
  border-radius: 15px;
}

.pokemoncard-container h1 {
  margin: 0.5rem;
  text-align: center;
  font-weight: bold;
}

.pokemoncard-container::first-letter {
  text-transform: uppercase;
}

.pokemoncard-details p {
  line-height: 0.5;
  margin-top: 0.25rem;
  font-weight: light;
  color: gray;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pokemoncard-details::first-letter {
  text-transform: uppercase;
}

.pokemoncard-details p span {
  padding: 0.5rem;
  color: white;
  font-weight: medium;
  margin-left: 1rem;
  text-transform: capitalize;
}
